// ==============================
// Custom style
// 自定义样式
// ==============================

/* 滚动条 */
::-webkit-scrollbar {
  width: 1rem;
  height: 0.5rem;
}

/* 首页头部 */
.home-profile {
  opacity: 0.85;
}

/* 首页文章摘要 */
.home[posts] .summary {
  margin-bottom: 0.1rem;
  padding-left: 2rem;
  padding-right: 2rem;
  opacity: 0.95;
}

/* 首页的分页页码 */
.pagination {
  margin-top: 0.1rem;
  margin-bottom: 0;
  padding: 1rem 0 0.5rem 0;
  opacity: 0.95;
}

/* 字体 */
.home[posts] .summary .content {
  color: #161209;
}

.single .single-title {
  font-size: 1.8rem;
}

/* 文章正文 */
.page.single {
  width: 51.5%;
  padding-left: 2rem;
  padding-right: 2rem;
  opacity: 0.95;
}

/* 文章脚部 */
.single .post-footer {
  margin-top: 0;
}

/* 页面脚部 */
.footer {
  display: block;
  border-top-width: 3px;
  border-top-style: solid;
  border-top-color: #a166ab;
  position: relative;
  max-width: 800px;
  width: 60%;
  margin: 0.1rem auto 0 auto;
  opacity: 0.95;
}

@media only screen and (max-width: 1440px) {
  .footer {
    width: 54.5%;
  }
}

@media only screen and (max-width: 1200px) {
  .footer {
    width: 50.5%;
  }
}

@media only screen and (max-width: 960px) {
  .footer {
    width: 77%;
  }
}

@media only screen and (max-width: 680px) {
  .footer {
    width: 95%;
  }
}

/* 目录 */
.toc {
  opacity: 0.95;
}

.toc .toc-content {
  font-size: 0.875rem;
}

/* 行内代码块 */
code {
  margin: 0 0.2rem;
  font-size: 0.9em;
  border: 1px solid #d6d6d6;
  border-radius: 0.2rem;
}

/* 预格式代码块(用tab键插入的代码块) */
pre code {
  margin: 0;
  border: none;
  font-size: 0.875rem;
}

/* 标题里的代码块样式 */
.page.single .content > h2 code {
  color: #f7ab01;
  background: transparent;
  border: none;
}

.mytag {
  position: relative;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  line-height: 2;
  margin: auto;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 3px 3px 6px 3px rgba(0, 0, 0, 0.5);
  overflow: hidden;
}
.mytag::before {
  content: "";
  position: relative;
  left: 0;
  right: 0;
  filter: blur(20px);
  z-index: -1;
  margin: -30px;
}

/* 搜索页面 */
.search {
  position: relative;
  padding-top: 3.5rem;
  padding-bottom: 1rem;
  max-width: 800px;
  width: 60%;
  margin: 0 auto;
  background: white;
  opacity: 0.95;
}

[theme="dark"] .search {
  position: relative;
  padding-top: 3.5rem;
  padding-bottom: 1rem;
  max-width: 800px;
  width: 60%;
  margin: 0 auto;
  background: #292a2d;
  opacity: 0.95;
}

[theme="dark"] .search header {
  background: #292a2d;
}
.search header {
  background: white;
}

[theme="dark"] .search header:hover,
.search header:hover {
  -webkit-box-shadow: none;
  box-shadow: none;
}

.search header h1 {
  padding-left: 1rem;
  background: white;
  opacity: 0.95;
}
[theme="dark"] .search header h1 {
  padding-left: 1rem;
  background: #292a2d;
  opacity: 0.95;
}

[theme="dark"] .search input,
.search input {
  height: initial;
  width: initial;
  color: initial;
  background: rgba(255, 255, 255, 0.8);
  margin: 0 0 0 1rem;
  border-width: 2px;
  border-style: inset;
  border-color: initial;
  border-image: initial;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
}

.search #search-results {
  padding-left: 1rem;
  padding-right: 1rem;
}

[theme="dark"] a:active,
[theme="dark"] a:hover {
  color: #2d96bd;
}

.search hr {
  margin-left: 1rem;
  margin-right: 1rem;
}
